export interface ValidationRule {
  regex: RegExp;
  message: string;
}

export interface RuleConfig {
  'lowercase-char': RuleConfigRuleField;
  'special-char': RuleConfigRuleField;
  digit: RuleConfigRuleField;
  'min-length': RuleConfigRuleField;
  'uppercase-char': RuleConfigRuleField;
}

interface RuleConfigRuleField {
  type: string;
  value: string | number;
  validators: Validator[];
}

interface Validator {
  type: string;
  expression: string;
}

export const normalCharacterRegex = /[a-z]/;
export const atleast8CharacterRegex = /.{8,}$/;
export const appearNumberChar = /(?=.*[0-9])/;

export interface IDataPassword {
  oldPassword: string;
  newPassword: string;
  confirmPassword: string;
}

export type TypeDataPassword = keyof IDataPassword;

interface ValidationResult {
  message: string;
  pass: boolean;
}

export const validationHint = (value: string, rules: ValidationRule[]) => {
  const results: ValidationResult[] = rules.map(rule => ({
    message: rule.message,
    pass: rule.regex.test(value),
  }));
  return results;
};
