import {TouchableOpacity, View} from 'react-native';
import HeaderCM from '../../components/Header/HeaderCM';
import {useTranslation} from 'react-i18next';
import {SafeAreaView} from 'react-native-safe-area-context';
import {useAccountInfoServices} from '../../../hooks/apiHooks/useAccountInfoServices';
import {isEmpty} from 'lodash';
import {useEffect, useMemo, useState} from 'react';
import {
  appearNumberChar,
  IDataPassword,
  normalCharacterRegex,
  RuleConfig,
  TypeDataPassword,
  validationHint,
  ValidationRule,
} from './helper';
import {RegexUtils} from '../../../utils';
import {KeyboardAwareScrollView} from 'react-native-keyboard-aware-scroll-view';
import styles from './styles';
import InputCM from '../../screens/review-eid/components/InputCM';
import {useAppSelector} from '../../../hooks/useRedux';
import ButtonCM from '../../components/Button';
import TextCM from '../../components/Text';
import {Icon} from '@ui-kitten/components';
import Toast from 'react-native-toast-message';
const ChangePasswordScreen = () => {
  const {t} = useTranslation();
  const {devicesInfo} = useAppSelector(state => state.common);
  const defaultMappingValidationRules: ValidationRule[] = [
    {
      regex: new RegExp(`.{${4},}$`),
      message: t('ST.ChangePassword.minimumLengthCharacters', {
        value: 4,
      }),
    },
  ];

  const mappingValidationRules = (rules: RuleConfig) => {
    const mappedRules: ValidationRule[] = [];
    for (const [key, value] of Object.entries(rules)) {
      if (
        key == 'lowercase-char' &&
        value.type === 'Boolean' &&
        value.value === 'true'
      ) {
        mappedRules.push({
          regex: normalCharacterRegex,
          message: t('ST.DigitalCertificate.atLeast1LowercaseCharacter'),
        });
      }
      if (
        key == 'uppercase-char' &&
        value.type === 'Boolean' &&
        value.value === 'true'
      ) {
        mappedRules.push({
          regex: /[A-Z]/,
          message: t('ST.ChangePassword.atLeast1UppercaseCharacter'),
        });
      }
      if (key == 'min-length' && value.type === 'Number') {
        mappedRules.push({
          regex: new RegExp(`.{${value.value},}$`),
          message: t('ST.ChangePassword.minimumLengthCharacters', {
            value: value.value,
          }),
        });
      }
      if (
        key == 'digit' &&
        value.type === 'Boolean' &&
        value.value === 'true'
      ) {
        mappedRules.push({
          regex: appearNumberChar,
          message: t('ST.DigitalCertificate.atLeast1NumericCharacter'),
        });
      }
      if (
        key == 'special-char' &&
        value.type === 'Boolean' &&
        value.value === 'true'
      ) {
        mappedRules.push({
          regex: RegexUtils.REGEX_SPECIAL_CHAR,
          message: t('ST.ChangePassword.atLeast1SpecialCharacter'),
        });
      }
    }
    return mappedRules;
  };
  const [rules, setRules] = useState<ValidationRule[]>([]);
  const [data, setData] = useState<IDataPassword>({
    oldPassword: '',
    newPassword: '',
    confirmPassword: '',
  });

  const [showPassword, setShowPassword] = useState({
    oldPassword: false,
    newPassword: false,
    confirmPassword: false,
  });
  const [loading, setLoading] = useState(false);
  const AccountServices = useAccountInfoServices();

  useEffect(() => {
    getRules();
  }, []);

  const getRules = async () => {
    try {
      const res = await AccountServices.getSecuriryPasswordRules();
      if (!isEmpty(res)) {
        const rulesData = (res.find(
          (item: any) => item.configType === 'complexPassword',
        )?.attrs as RuleConfig) ?? {'min-length': {type: 'Number', value: 4}};
        setRules(mappingValidationRules(rulesData));
      } else {
        setRules(defaultMappingValidationRules);
      }
    } catch (error) {
      console.log(error);
    }
  };
  const valHint = useMemo(
    () => validationHint(data.newPassword ?? '', rules),
    [data.newPassword, rules],
  );

  const isDisableButton =
    valHint.some(item => !item.pass) ||
    !data.oldPassword ||
    !data.newPassword ||
    !data.confirmPassword;

  const renderHintext = () => {
    return (
      <View style={styles.viewHint}>
        {valHint.map((item, index) => {
          return (
            <View key={index} style={styles.itemHint}>
              {item.pass ? (
                <Icon
                  fill={'#219342'}
                  name="checkmark-circle"
                  width={16}
                  height={16}
                />
              ) : (
                <Icon
                  fill={'#DA3E33'}
                  name="close-circle"
                  width={16}
                  height={16}
                />
              )}
              <TextCM style={styles.txtHint}>{item.message}</TextCM>
            </View>
          );
        })}
      </View>
    );
  };

  const handleChangeData = (type: TypeDataPassword, value: string) => {
    setData(prev => ({...prev, [type]: value}));
  };
  const handleChangeShowData = (type: TypeDataPassword, value: boolean) => {
    setShowPassword(prev => ({...prev, [type]: value}));
  };

  const mapMessageError = (code: string) => {
    switch (code) {
      case 'validations.error.passwordnotcorrect':
        return t('ST.ChangePassword.passwordnotcorrect');
      case 'error.userPolicyPasswordDuplicate':
        return t('ST.ChangePassword.userPolicyPasswordDuplicate');
      default:
        // temporary message
        return 'error change password';
    }
  };

  const onSubmit = async () => {
    try {
      if (data.newPassword !== data.confirmPassword) {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('ST.passwordNotMatch'),
        });
        return;
      }
      setLoading(true);
      const body = {
        currentPassword: data.oldPassword,
        newPassword: data.newPassword,
        reNewPassword: data.confirmPassword,
        contactTrace: devicesInfo,
      };
      const res = await AccountServices.changePassword(body);
      if (res.data) {
        Toast.show({
          type: 'success',
          text1: t('CM.noti'),
          text2: t('ST.changePasswordSuccessfully'),
        });
        setData({
          oldPassword: '',
          newPassword: '',
          confirmPassword: '',
        });
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: mapMessageError(res.error.title),
        });
      }
    } catch (err: any) {
    } finally {
      setLoading(false);
    }
  };
  return (
    <SafeAreaView edges={['bottom']} style={{flex: 1, backgroundColor: '#fff'}}>
      <HeaderCM
        style={{
          backgroundColor: '#6F2CED',
        }}
        contentStyle={{
          backgroundColor: '#6F2CED',
        }}
        title={t('ST.TxtChangePassword')}
      />

      <KeyboardAwareScrollView style={styles.mainContainer}>
        <View style={{rowGap: 10}}>
          <InputCM
            secureTextEntry={!showPassword.oldPassword}
            label={t('ST.DigitalCertificate.currentPassword')}
            placeholder={t('ST.DigitalCertificate.typeCurrentPassword')}
            isRequired={true}
            value={data.oldPassword}
            onChangeText={value => handleChangeData('oldPassword', value)}
            accessoryRightIcon={
              <TouchableOpacity
                style={{paddingLeft: 10}}
                onPress={() =>
                  handleChangeShowData('oldPassword', !showPassword.oldPassword)
                }>
                <Icon
                  name={showPassword.oldPassword ? 'eye' : 'eye-off'}
                  width={16}
                  height={16}
                />
              </TouchableOpacity>
            }
          />
          <InputCM
            secureTextEntry={!showPassword.newPassword}
            label={t('ST.DigitalCertificate.NewPassword')}
            isRequired={true}
            placeholder={t('ST.DigitalCertificate.typeNewPassword')}
            value={data.newPassword}
            onChangeText={value => handleChangeData('newPassword', value)}
            accessoryRightIcon={
              <TouchableOpacity
                style={{paddingLeft: 10}}
                onPress={() =>
                  handleChangeShowData('newPassword', !showPassword.newPassword)
                }>
                <Icon
                  name={showPassword.newPassword ? 'eye' : 'eye-off'}
                  width={16}
                  height={16}
                />
              </TouchableOpacity>
            }
          />
          {renderHintext()}
          <InputCM
            secureTextEntry={!showPassword.confirmPassword}
            label={t('ST.DigitalCertificate.typeConfirmPassword')}
            isRequired={true}
            placeholder={t('ST.DigitalCertificate.typeConfirmPassword')}
            value={data.confirmPassword}
            onChangeText={value => handleChangeData('confirmPassword', value)}
            accessoryRightIcon={
              <TouchableOpacity
                style={{paddingLeft: 10}}
                onPress={() =>
                  handleChangeShowData(
                    'confirmPassword',
                    !showPassword.confirmPassword,
                  )
                }>
                <Icon
                  name={showPassword.confirmPassword ? 'eye' : 'eye-off'}
                  width={16}
                  height={16}
                />
              </TouchableOpacity>
            }
          />
        </View>
      </KeyboardAwareScrollView>
      <ButtonCM
        disabled={isDisableButton}
        style={{
          backgroundColor: '#6F2CED',
          marginHorizontal: 16,
        }}
        styleLabel={{
          color: '#fff',
        }}
        label={t('CM.confirm')}
        onPress={onSubmit}
        loading={loading}
      />
    </SafeAreaView>
  );
};

export default ChangePasswordScreen;
