import {View} from 'react-native';
import HeaderCM from '../../../../components/Header/HeaderCM';
import {useTranslation} from 'react-i18next';
import TextCM from '../../../../components/Text';
import {IconChangePhone} from '../../../../../assets/images';
import InputCM from '../../../../screens/review-eid/components/InputCM';
import {useState} from 'react';
import {RegexUtils} from '../../../../../utils';
import {KeyboardAwareScrollView} from 'react-native-keyboard-aware-scroll-view';
import ButtonCM from '../../../../components/Button';
import {useAppDispatch, useAppSelector} from '../../../../../hooks/useRedux';
import {useAccountInfoServices} from '../../../../../hooks/apiHooks/useAccountInfoServices';
import {accountActions} from '../../../../../redux/slices';
import {isEmpty} from 'lodash';
import styles from './styles';
import Toast from 'react-native-toast-message';

type Props = {
  onClose: () => void;
};

const ChangePhone = ({onClose}: Props) => {
  const {t} = useTranslation();
  const dispatch = useAppDispatch();
  const {dataAccount} = useAppSelector(state => state.account);
  const [phone, setPhone] = useState(dataAccount.phone || '');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const AccountServices = useAccountInfoServices();

  const handleChangePhone = (value: string) => {
    setPhone(value);
    if (value?.length === 10 || value?.length === 12) {
      setError('');
    } else {
      setError(t('CM.validPhone'));
    }
  };

  const handleUpdatePhone = async () => {
    try {
      setLoading(true);
      const dataResp = await AccountServices.updateAccount({
        ...dataAccount,
        phone: phone,
      });
      if (dataResp) {
        Toast.show({
          type: 'success',
          text1: t('CM.info'),
          text2: t('CM.phoneUpdatedSuccessfully'),
        });
        dispatch(accountActions.setDataAccountSuccess(dataResp));
        onClose();
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.info'),
          text2: t('CM.phoneUpdatedFailed'),
        });
      }
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  return (
    <View style={{flex: 1}}>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        contentStyle={{
          backgroundColor: '#fff',
        }}
        titleStyle={{
          color: '#000810',
        }}
        fillIconBackLeft="#000810"
        title={t('ST.changePhone')}
        onPressIconLeft={onClose}
      />

      <KeyboardAwareScrollView>
        <View style={{padding: 16}}>
          <View style={styles.ctnHeader}>
            <TextCM style={styles.textHeader}>
              {t('ST.headerChangePhone')}
            </TextCM>
            <TextCM style={styles.textDesHeader}>
              {t('ST.descriptionsHeaderChangePhone')}
            </TextCM>
          </View>

          <View style={styles.ctnIcon}>
            <IconChangePhone width={127} height={155} />
          </View>
          <TextCM style={styles.textType}>{t('ST.typeNewPhone')}</TextCM>
          <TextCM style={styles.textDesType}>{t('ST.desTypeNewPhone')}</TextCM>

          <InputCM
            value={phone}
            onChangeText={handleChangePhone}
            keyBoardType={'numeric'}
            placeholder={t('ST.typeNewPhone')}
            captionText={t(error)}
          />
        </View>
      </KeyboardAwareScrollView>
      <ButtonCM
        disabled={!isEmpty(error) ? true : false}
        style={styles.btn}
        styleLabel={styles.labelBtn}
        label={t('CM.save')}
        onPress={handleUpdatePhone}
        loading={loading}
      />
    </View>
  );
};

export default ChangePhone;
