import {useState} from 'react';
import {Image, View} from 'react-native';
import ImageCropPicker from '../../../../components/ImageCropPicker';
import {String} from '../../../../../constants';
import ButtonCM from '../../../../components/Button';
import {useAccountInfoServices} from '../../../../../hooks/apiHooks/useAccountInfoServices';
import styles from './styles';
import {useTranslation} from 'react-i18next';
import {last} from 'lodash';
import {useAppDispatch, useAppSelector} from '../../../../../hooks/useRedux';
import {accountActions} from '../../../../../redux/slices';

type Props = {
  setAvatar: (url: string) => void;
  avatarUrl: string;
};

const AvatarModal = ({setAvatar, avatarUrl}: Props) => {
  const {t} = useTranslation();
  const dispatch = useAppDispatch();
  const {dataAccount} = useAppSelector(state => state.account);
  const [loading, setLoading] = useState('');
  const AccountServices = useAccountInfoServices();

  const handlePickImage = async () => {
    try {
      setLoading('uploadAvatar');
      const data = (await ImageCropPicker.ChoosePhoto({
        cropping: true,
      })) as any;
      if (data) {
        const body = new FormData();
        const file = {
          uri: data.path,
          type: data.mime,
          name: last(data?.path?.split('/')),
        };
        body.append('file', file);
        body.append('zoom', 1);

        const dataResp = await AccountServices.updateAccountAvatar(body);
        if (dataResp) {
          setAvatar(dataResp.imageUrl);
          dispatch(
            accountActions.setDataAccountSuccess({
              ...dataAccount,
              imageUrl: dataResp.imageUrl,
            }),
          );
        }
      }
    } catch (error) {
    } finally {
      setLoading('');
    }
  };

  const handleDeleteAvatar = async () => {
    try {
      setLoading('deleteAvatar');
      const dataResp = await AccountServices.deleteAvatar();
      if (dataResp) {
        setAvatar('');
      }
    } catch (error) {
    } finally {
      setLoading('');
    }
  };

  return (
    <View>
      <View style={styles.ctnImage}>
        <Image
          width={200}
          height={200}
          borderRadius={100}
          source={{
            uri: avatarUrl || String.DEFAULT_IMAGE,
          }}
        />
      </View>
      <View style={styles.ctnBtn}>
        <ButtonCM
          style={styles.btnDel}
          label={t('ST.delAvatar')}
          styleLabel={styles.textDel}
          onPress={handleDeleteAvatar}
          loading={loading === 'deleteAvatar'}
        />
        <ButtonCM
          style={styles.btnEdit}
          styleLabel={styles.textEdit}
          label={t('ST.editAvatar')}
          onPress={handlePickImage}
          loading={loading === 'uploadAvatar'}
        />
      </View>
    </View>
  );
};

export default AvatarModal;
