import {SafeAreaView} from 'react-native-safe-area-context';
import HeaderCM from '../../components/Header/HeaderCM';
import {useTranslation} from 'react-i18next';
import {Image, ScrollView, TouchableOpacity, View} from 'react-native';
import TextCM from '../../components/Text';
import {useAppSelector} from '../../../hooks/useRedux';
import {IconAccount, IconCamera} from '../../../assets/images';
import React, {useEffect, useMemo, useState} from 'react';
import InfoBodyScreen from '../../screens/review-eid/components/Info';
import {Icon} from '@ui-kitten/components';
import {CommonUtils, DateUtils} from '../../../utils';
import styles from './styles';
import BottomSheetCM from '../../components/BottomSheet';
import AvatarModal from './components/Avatar';
import ModalFullScreen from '../../components/ModalFullScreen';
import ChangePhone from './components/ChangePhone';
import {BASE_URL_EACCOUNT} from '../../../services/httpClient';

const InformationScreen = () => {
  const {t} = useTranslation();
  const [showModal, setShowModal] = useState('');
  const {dataAccount} = useAppSelector(state => state.account);
  const [avatarUrl, setAvatarUrl] = useState('');
  const dataInfo: any[] = useMemo(() => {
    return [
      {
        key: 'Họ và tên',
        type: 'normal',
        value: {
          name:
            '' + dataAccount.firstName + ' ' + dataAccount.lastName || '---',
        },
      },
      // {
      //   key: 'Giới tính',
      //   type: 'normal',
      //   value: {
      //     name: dataAccount.gender || '---',
      //   },
      // },
      // {
      //   key: 'Ngày sinh',
      //   type: 'normal',
      //   value: {
      //     name: DateUtils.convertDateToDDMMYYYY(dataAccount?.dob) || '---',
      //   },
      // },
      // {
      //   key: 'Mã số thuế',
      //   type: 'normal',
      //   value: {
      //     name: dataAccount.taxCodeOrg || '---',
      //   },
      // },
      {
        key: 'Số điện thoại',
        type: 'normal',
        value: {
          name: dataAccount.phone || '---',
          withIcon: (
            <TouchableOpacity
              hitSlop={15}
              onPress={() => setShowModal('changePhone')}>
              <Icon
                fill={'#00204DB2'}
                name="edit-2-outline"
                width={20}
                height={20}
              />
            </TouchableOpacity>
          ),
        },
      },
    ];
  }, [dataAccount]);

  useEffect(() => {
    getAvatar();
  }, []);

  const getAvatar = async () => {
    try {
      const dataResp = await CommonUtils.downloadFile(
        `avatar-${new Date().getTime()}.jpg`,
        `${BASE_URL_EACCOUNT}/services/uaa/api/account/avatar`,
        false,
      );
      setAvatarUrl(dataResp.uri);
    } catch (error) {}
  };

  const onCloseModal = () => setShowModal('');

  return (
    <React.Suspense fallback>
      <SafeAreaView style={styles.ctnMain} edges={['bottom']}>
        <HeaderCM
          style={{
            backgroundColor: '#6F2CED',
          }}
          contentStyle={{
            backgroundColor: '#6F2CED',
          }}
          title={t('ST.TxtAccountInformation')}
        />

        <View style={{marginBottom: 30}}>
          <View style={styles.ctnheader}>
            <View
              style={CommonUtils.generateBoxShadowStyle(
                0,
                4,
                '#1A3C5412',
                5,
                3,
                5,
                '#1A3C5412',
              )}>
              <View style={styles.ctnView}>
                <View style={styles.ctnOrg}>
                  <View style={{flex: 1}} />
                  <View style={styles.ctnOrgView}>
                    <TextCM style={styles.textOrg}>Doanh nghiệp</TextCM>
                  </View>
                </View>
                <View style={styles.ctnInfo}>
                  <TouchableOpacity
                    onPress={() => setShowModal('avatar')}
                    style={{
                      position: 'relative',
                    }}>
                    <Image
                      width={50}
                      height={50}
                      borderRadius={25}
                      source={{
                        uri:
                          avatarUrl ||
                          'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
                      }}
                    />
                    <View style={{position: 'absolute', bottom: 0, right: 0}}>
                      <IconCamera width={16} height={16} />
                    </View>
                  </TouchableOpacity>
                  <View>
                    <TextCM style={styles.textname}>
                      {'' + dataAccount.firstName + ' ' + dataAccount.lastName}
                    </TextCM>
                    <TextCM style={styles.textEmail}>
                      {dataAccount.email || '---'}
                    </TextCM>
                  </View>
                </View>
              </View>
            </View>
          </View>
        </View>

        <ScrollView style={styles.ctnMainIfo}>
          <View style={styles.ctnBody}>
            <View style={styles.ctnbodyHeader}>
              <View style={styles.ctnBodyHeaderLeft}>
                <IconAccount width={20} height={22} />
                <TextCM style={styles.textHeader}>{t('ST.information')}</TextCM>
              </View>
            </View>
            <InfoBodyScreen dataInfo={dataInfo} />
          </View>
        </ScrollView>

        {avatarUrl && (
          <BottomSheetCM
            isVisible={showModal === 'avatar'}
            title={t('ST.changeAvatar')}
            onClose={onCloseModal}
            renderContent={
              <AvatarModal avatarUrl={avatarUrl} setAvatar={setAvatarUrl} />
            }
          />
        )}

        <ModalFullScreen
          isVisible={showModal === 'changePhone'}
          children={<ChangePhone onClose={onCloseModal} />}
        />
      </SafeAreaView>
    </React.Suspense>
  );
};

export default InformationScreen;
